<template>
  <div class="playsong">
    <div class="needle"></div>
    <div class="backgr">
      <div class="scle"></div>
    </div>
    <div class="part1">
      <img src="../image/logo.png" alt srcset class="logo" />
      <div class="imagemisic">
        <div class="disc" @click="stopmusic">
          <div class="setrount">
            <img :src="item.song.album.picUrl" v-if="item.song" />
            <img :src="item.al.picUrl" v-if="item.al" />
          </div>

          <!-- <img src="../image/logo.jpg" v-if="imgshow" class="imgs"> -->
          <i class="el-icon-video-play" v-if="this.isplayshow"></i>
        </div>
      </div>
      <div class="lysic"></div>
    </div>
    <div class="part2">
      <div class="part2icon" v-if="false">
        <van-icon name="like-o" @click="clickicon1" />
        <van-icon name="like-o" @click="clickicon2" />
        <van-icon name="like-o" info="99+" @click="clickicon3" />
        <van-icon name="like-o" @click="clickicon4" />
      </div>
      <div class="part2audio">
        <div class="progress"  v-if="false">
          <span>{{nowtime}}</span>
          <div class="inputslider" v-if="false">
            <el-slider v-model="value1" input-size="mini"></el-slider>
          </div>

          <span>{{time}}</span>
        </div>
        <div class="songplaycontent" >
          <i class="el-icon-refresh" @click="getlunxu"></i>
          <i class="el-icon-caret-left" @click="turnlast"></i>
          <i class="el-icon-video-play" @click="turnplay" v-if="this.isplayshow"></i>
          <i class="el-icon-video-pause" @click="turnplay" v-if="!this.isplayshow"></i>
          <i class="el-icon-caret-right" @click="turnnext"></i>
          <i class="el-icon-s-fold" @click="showfoldmusic"></i>
        </div>
        <div class="musiclist" v-if="showlist">
          <!-- <span class="radow" @click="setradow">随机播放</span> -->
          <!-- <i class="el-icon-s-unfold unfoldmusic" @click="showfoldmusic2"></i> -->
          <div class="setover">
            <ul>
              <li v-for="(item, index) in musiclist" :key="index" @click="playsong(item,musiclist)">
                <p>
                  <span v-if="item.song">{{item.song.artists[0].name}}-</span>
                  <span v-if="item.ar">{{item.ar[0].name}}-</span>
                  {{item.name}}
                </p>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="less" scoped>
@keyframes imgrotate {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}
.playsong {
  .backgr {
    width: 100%;
    height: 100%;
    max-height: 800px;
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    z-index: -2;
    .scle {
      width: 100%;
      height: 100%;
      background: url("../image/blur.jpg") no-repeat;
      background-size: 100% 100%;
      background-position: 50%;
      transform: scale(1.5);
    }
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-color: rgba(0, 0, 0, 0.5);
    }
  }
  .needle {
    background: url("../image/needle.png");
    width: 105px;
    height: 150px;
    background-size: cover;
    position: absolute;
    top: 0;
    left: 45%;
    z-index: 999;
    transform: rotateZ(0deg);
    transform-origin: top;
    transition: all 1s;
    &.move {
      transform: rotateZ(-20deg);
    }
  }
  .part1 {
    width: 100%;
    height: 75%;
    position: relative;
    .logo {
      position: absolute;
      top: -34px;
      left: 20px;
      width: 80px;
      height: 20px;
    }
    .imagemisic {
      margin-top: 50px;
      width: 100%;
      display: flex;
      justify-content: center;

      .disc {
        width: 250px;
        height: 250px;
        background: url("../image/disc.png");
        background-size: contain;

        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        .setrount {
          width: 150px;
          height: 150px;
          border-radius: 50%;
          overflow: hidden;
          img {
            width: 100%;
            height: 100%;

            animation: imgrotate 20s linear infinite;
            animation-play-state: running;
            &.pauses {
              animation-play-state: paused;
            }
          }
        }
        i {
          position: absolute;
          top: 100px;
          left: 100px;
          font-size: 50px;
          display: block;
          color: white;
        }
      }
    }
    // .lysic {
    //   min-height: 60px;
    //   width: 100%;
    //   background: yellow;
    // }
  }
  .part2 {
    position: absolute;
    bottom: 10px;
    left: 0;
    width: 100%;
    .part2icon {
      display: flex;
      justify-content: space-around;
      color: white;
    }
    .part2audio {
      position: relative;
      .progress {
        padding: 10px 10%;
        display: flex;
        align-items: center;
        span {
          width: 10%;
          color: white;
        }
        .inputslider {
          display: inline-block;
          width: 80%;
        }
      }
      .songplaycontent {
       margin: 20px auto;
        display: flex;
        justify-content: space-around;
        align-items: center;
        i {
          color: white;
          font-size: 28px;
        }
      }
      .musiclist {
        width: 100%;
        height: 250px;
        position: absolute;
        bottom: 40px;
        left: 0;

        background: white;
        overflow: scroll;
        // border-top: 1px solid gray;
        color: rgb(20, 20, 20);
        .radow {
          float: left;
          line-height: 40px;
          font-size: 14px;
          padding-left: 10px;
        }
        .unfoldmusic {
          font-size: 25px;
          line-height: 40px;
          padding-left: 20px;
        }
        .setover {
          clear: both;
          border-top: 1px solid gray;
          ul {
            list-style: none;
            margin: 0;
            padding: 0;
            li {
              // border-bottom: 1px solid gray;

              p {
                line-height: 35px;
                font-size: 14px;
                margin: 0;
                padding-left: 20px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                word-break: normal;
              }
              span {
                font-size: 10px;
                color: grey;
              }
            }
          }
        }
      }
    }
  }
}
</style>

<script>
export default {
  data() {
    return {
      isplayshow: false,
      isclick: false,
      nowtime: 0,
      time: null,
      value1: 20,
      showlist: false
    };
  },
  created() {
    this.$store.commit("SetIscomponentShowFalse");

    var arraytime = this.setsecond(
      this.$store.state.currentMusic.song.duration
    );
    if (arraytime[1] < 10) {
      this.time = arraytime[0] + ":0" + arraytime[1];
    } else {
      this.time = arraytime[0] + ":" + arraytime[1];
    }
  },
  mounted() {
    var needle = document.querySelector(".playsong .needle");
    var imgmove = document.querySelector(".setrount img");

    if (this.isplay) {
      //设置指针偏转
      needle.className = "needle";
      // 设置图片动画停止
      imgmove.className = "";
      this.isplayshow = false;
    } else {
      //设置指针偏转
      needle.className = "needle move";
      // 设置图片动画停止
      imgmove.className = "pauses";
      this.isplayshow = true;
    }
    //判断是否随机歌曲
    var firsti = document.querySelectorAll(".songplaycontent i")[0];
    if (this.$store.state.NotChageMusicList == this.$store.state.Musiclist) {
      firsti.style.color = "white";
    } else {
      firsti.style.color = "red";
    }
  },
  beforeRouteLeave(to, from, next) {
    this.$store.commit("SetIscomponentShowTrue");
    next();
  },
  computed: {
    item: function() {
      return this.$store.state.currentMusic;
    },
    isplay: function() {
      return this.$store.state.isplay;
    },
    musiclist: function() {
      return this.$store.state.NotChageMusicList;
    }
  },
  watch: {
    item:function(){
       var arraytime = this.setsecond(
      this.$store.state.currentMusic.song.duration
    );
    if (arraytime[1] < 10) {
      this.time = arraytime[0] + ":0" + arraytime[1];
    } else {
      this.time = arraytime[0] + ":" + arraytime[1];
    }


     //设置指针偏转
      var needle = document.querySelector(".playsong .needle");
      
        needle.className = "needle";
      
      // 设置图片动画停止
      var imgmove = document.querySelector(".setrount img");
      
        imgmove.className = "";
      
      this.isplayshow = false;
    }
  },
  methods: {
    stopmusic() {
      //设置指针偏转
      var needle = document.querySelector(".playsong .needle");
      if (!this.isplay) {
        needle.className = "needle";
      } else {
        needle.className = "needle move";
      }
      // 设置图片动画停止
      var imgmove = document.querySelector(".setrount img");
      if (!this.isplay) {
        imgmove.className = "";
        this.isplayshow =false;
      } else {
        imgmove.className = "pauses";
        this.isplayshow =true
      }
      // this.isplayshow = !this.isplayshow;
      this.$store.commit("SetisplayNot");
      // this.isclick = true;
    },
    clickicon1() {
      console.log("clickicon1");
    },
    clickicon2() {
      console.log("clickicon2");
    },
    clickicon3() {
      console.log("clickicon3");
    },
    clickicon4() {
      console.log("clickicon4");
    },
    setsecond(second) {
      let alltime = _.round(second / 1000);
      var minute = _.floor(alltime / 60);
      var second = alltime % 60;
      // console.log(minute);
      // console.log(second);
      var arraytime = [];
      arraytime.push(minute);
      arraytime.push(second);
      return arraytime;
    },
    getlunxu() {
      var firsti = document.querySelectorAll(".songplaycontent i")[0];
      if (firsti.style.color == "red") {
        firsti.style.color = "white";
        this.$store.commit(
          "SetcurrentMusiclist",
          this.$store.state.NotChageMusicList
        );
      } else {
        firsti.style.color = "red";
        this.$store.commit(
          "SetcurrentMusiclist",
          _.shuffle(this.$store.state.Musiclist)
        );
      }
      // firsti.style.color="red";
    },
    turnlast: function() {
      this.$store.commit("TurnlastSong");
      
    },
    turnnext: function() {
      this.$store.commit("TurnnextSong");
      
    },
    turnplay() {
      this.stopmusic();
    },
    showfoldmusic: function() {
      this.showlist = !this.showlist;
    },
    showfoldmusic2: function() {
      if (this.showlist) {
        this.showlist = false;
      }
    },
     playsong(item, list) {
      // console.log(item.id);
      let that = this;

      axios
        .get("/song/url", {
          params: {
            id: item.id
          }
        })
        .then(function(response) {
          // console.log(response);
          that.$store.commit("Setcurrentmusicurl", response.data.data[0]);
          that.$store.commit("Setmusicid", item.id);
          that.$store.commit("SetcurrentMusic", item);
          that.$store.commit("SetcurrentMusiclist", list);
          that.$store.commit("SetNotChageMusicList", list);
          // console.log(that.personalizedList);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
  }
};
</script>
